@import "../mixins";
.display1{
  font-size: 4.25em;
  line-height: 115%;
  font-weight: 700;
}

.display2{
  font-size: 3.375em;
  line-height: 122%;
  font-weight: 700;
}

.accent-text{
  color: $primary;
}

.text16{
  @include grid100gap(1.125em);
  p{
    font-size: 1em;
    line-height: 162%;
  }
}

.text14{
  @include grid100gap(1.125em);
  p{
    font-size: .875em;
    line-height: 186%;
  }
}

.text12{
  @include grid100gap(1.125em);
  p{
    font-size: .75em;
    line-height: 183%;
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 3em;
  }

  .display2{
    font-size: 2.75em;
  }
}


@media screen and (max-width: 576px){
  .display1{
    font-size: 2.375em;
  }

  .display2{
    font-size: 2.375em;
  }
}
