.footer{
  padding: 5.625em 0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  &__inner{
    margin: 0 auto;
    width: 100%;
    max-width: 58.3125em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img{
    width: 5em;
    height: 5em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__title{
    font-weight: 600;
    font-size: 2.375em;
    line-height: 121%;
    margin-top: 1em;
  }

  .contacts{
    margin-top: 2.375em;
    width: 100%;
  }

  &__desc{
    width: 100%;
    max-width: 37.875em;
    margin-top: 2.375em;
    color: #5E5E5E;
    text-align: center;
  }
  &__logo{
    margin-top: 2.375em;
    display: block;
    width: 7.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__by{
    margin-top: 2.71em;
    text-align: center;
    font-size: .875em;
    line-height: 157%;
    a{
      color: $primary;
    }
  }
}

.contacts{
  display: flex;
  justify-content: space-between;
  &__title{
    font-size: .875em;
    line-height: 186%;
    color: #777777;
  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: .25em;
    font-weight: 500;
    span{
      font-size: 1.125em;
      line-height: 144%;
    }
  }
  &__icon{
    flex-shrink: 0;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.125em 0;
    min-height: unset;
  }

  .contacts{
    max-width: 29.25em;
    display: grid;
    grid-template-columns: auto 11em;
    gap: .625em;
  }
}



@media screen and (max-width: 576px){
  .footer{
    padding: 2em 1em;
  }

  .contacts{
    max-width: 13.75em;
    grid-template-columns: 100%;
  }
}
