@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    padding: 0 1.5em;
    height: 3.375em;
    font-weight: 700;
    background-color: $primary;
    color: #fff;
    img{
        width: 1.125em;
        margin-right: 1em;
        flex-shrink: 0;
        display: block;
    }
    span{
        font-size: 1.125em;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}
