@import "../mixins";
.main{
  background-color: $gray;
  position: relative;
  background-image: url(../img/main-bg.svg);
  background-size: 100% auto;
  background-position: left bottom;
  background-repeat: no-repeat;
  &__inner{
    position: relative;
    z-index: 2;
    min-height: 100vh;
    padding: 10.125em 0 6.125em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  &__subtitle{
    font-size: 3em;
    line-height: 142%;
    font-weight: 500;
  }
  &__desc{
    margin: 1.75em auto 0;
    max-width: 41.125em;
    width: 100%;
    p{
      font-size: 1.125em;
      line-height: 189%;
    }
  }
  &__socials,
  &__contacts{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.75em;
  }
  &__contacts{
    &-item{
      margin: 0 .875em;
      display: flex;
      align-items: center;
      font-weight: 500;
      span{
        font-size: 1.125em;
        line-height: 144%;
      }
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 1.5em;
      display: block;
      img{
        max-width: 100%;
      }
    }
  }
  &__socials{
    &-item{
      margin: 0 .5em;
      width: 3em;
      flex-shrink: 0;
      &:hover{
        img{
          transform: translateY(-.8em);
        }
      }
      img{
        width: 100%;
        transition: all .3s;
      }
    }
  }
  &__nav{
    @include center;
    margin-top: 2.375em;
  }
  &__img{
    display: none;
  }
}

.about{
  z-index: 2;
  padding: 8.375em 0;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: auto;
    display: block;
  }
  &__inner{
    max-width: 31em;
  }
  &__top{
    padding-bottom: 1.125em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 3.125em;
      background-color: $black;
    }
  }
  &__subtitle{
    font-size: 2.25em;
    line-height: 122%;
  }
  &__desc{
    margin-top: 1.125em;
  }
}

.info{
  padding: 2.75em 0 0;
  position: relative;
  margin-top: -.2em;
  overflow: hidden;
  background-color: $gray;
  &__inner{
    width: 100%;
    max-width: 48.5em;
  }
  &__wrap{
    margin-top: .875em;
    display: grid;
    grid-template-columns: auto 14.75em;
    gap: 3em;
  }
  &__right{
    padding: .75em 0;
  }
  &__text{
    @include grid100gap(.75em);
    .text12{
      color: #7C7C7C;
    }
  }
  &__nav{
    padding: .75em 0 .75em 3em;
    border-left: 1px solid #E3E3E3;
  }
  &__img{
    width: 100%;
    margin-top: 3.5em;
    display: block;
    &._mob{
      display: none;
    }
  }
  &__img2{
    position: absolute;
    right: -.06em;
    top: 0;
    width: 26em;
    display: block;
  }
}

.investment{
  padding: 2.25em 0 4.125em;
  &__inner{
    display: grid;
    grid-template-columns: 28.3125em calc(100% - 30.6875em);
    align-items: center;
    gap: 2.375em;
  }
  &__img{
    img{
      width: 100%;
    }
  }
  .display2{
    line-height: 98%;
  }
  &__desc{
    margin-top: 1.125em;
    max-width: 26.875em;
  }
  &__quote{
    margin-top: 1.125em;
    padding: .5em 0 .5em 1.5em;
    border-left: 2px solid #FFD687;
    p{
      font-size: 1em;
      line-height: 162%;
    }
  }
  &__more{
    margin-top: 1.125em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: $primary;
    &:hover{
      color: $black;
    }
    svg{
      width: .625em;
      height: auto;
      margin-left: .5em;
    }
    span{
      font-size: 1em;
      line-height: 162%;
    }
  }
}


@media screen and (max-width: 1300px){
  .about{
    &__img{
      right: -5em;
    }
  }

  .info{
    &__img2{
      right: -5.06em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    background-image: url(../img/main-bg2.svg);
    &__inner{
      padding: 14.0625em 0 4em;
    }
    &__subtitle{
      font-size: 2.125em;
      margin-top: .25em;
    }
    &__desc{
      max-width: 30em;
      p{
        font-size: 1em;
        line-height: 150%;
      }
    }
    &__nav{
      margin-top: 1.75em;
    }
  }

  .about{
    padding: 1.5em 0 2.1875em;
    &__inner{
      max-width: 20em;
    }
    &__img{
      width: 25em;
      height: auto;
      right: -2.375em;
      top: unset;
      bottom: 0;
    }
    &__subtitle{
      font-size: 1.75em;
    }
  }

  .info{
    padding-top: 2.125em;
    &__inner{
      max-width: 31em;
    }
    &__wrap{
      margin-top: 1.625em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__right{
      padding: 0;
    }
    &__text{
      gap: 1.5em;
    }
    &__img{
      margin-top: 2.125em;
    }
    &__img2{
      width: 13.5em;
      right: -1.55em;
    }
    &__nav{
      padding: 0;
      border: none;
      display: flex;
    }
  }

  .investment{
    padding: 2.5em 0 4.375em;
    &__inner{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__img{
      max-width: 35.25em;
      width: 100%;
      margin: 0 auto;
    }
    .display2{
      line-height: 100%;
    }
    &__desc,
    &__quote{
      max-width: 100%;
      p{
        font-size: .875em;
        line-height: 186%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    background-image: none;
    overflow: hidden;
    &__inner{
      padding: 7em .375em 9.625em;
    }
    &__subtitle{
      font-size: 1.75em;
      margin: 0;
    }
    &__desc{
      margin-top: 1.125em;
      p{
        line-height: 162%;
      }
    }
    &__contacts{
      margin-top: 1.125em;
      @include grid100gap(1em);
      &-item{
        margin: 0;
      }
      &-icon{
        width: 1.25em;
        margin-right: 1.25em;
        text-align: left;
      }
    }
    &__socials{
      margin-top: 1.125em;
    }
    &__nav{
      margin: 0;
    }
    &__btn{
      position: absolute;
      left: -1.25em;
      bottom: 0;
      width: calc(100% + 2.5em);
    }
    &__img{
      position: absolute;
      display: block;
      width: calc(100% - 1.625em);
      top: 100%;
      transform: translateY(-7.625em);
      right: .375em;
    }
  }

  .about{
    padding: 1.625em 0 0;
    &__img{
      position: static;
      width: calc(100% + 1.25em);
      margin: 2.5em -1.25em 0 0;
    }
    &__subtitle{
      font-size: 1.5em;
      line-height: 150%;
    }
  }

  .info{
    margin: 0;
    &__wrap{
      margin-top: 1em;
    }
    &__right{
      display: none;
    }
    &__text{
      gap: 2.25em;
    }
    &__img{
      margin-top: .875em;
      &._desktop{
        display: none;
      }
      &._mob{
        display: block;
      }
    }
    &__img2{
      display: none;
    }
  }

  .investment{
    padding: 2.125em 0;
    overflow: hidden;
    &__inner{
      gap: 1.875em;
    }
    &__img{
      width: 139%;
      margin: 0 0 0 -19.5%;
    }
  }
}
