@import "../mixins";
.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1.6875em 0;
  z-index: 10;
  transition: all .3s;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:after{
      content: '';
      display: block;
      width: 7.5em;
    }
  }
  &__logo{
    display: block;
    width: 7.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &._fix{
    background-color: #fff;
    box-shadow: 0 .25em 1.5625em rgba(0, 0, 0, 0.07);
    padding: .75em 0;
    .header{
      &__inner{
        &:after{
          display: none;
        }
      }
      &__logo{
        width: 4.375em;
      }
    }
  }
}


.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    color: $grayDark;
    padding: .25em 0;
    border-bottom: 1px solid transparent;
    span{
      font-size: 1em;
      line-height: 162%;
    }
    &:not(:last-child){
      margin-right: 1.75em;
    }
    &._active,
    &:hover{
      border-color: $black;
      color: $black;
    }
  }
}

.burger,
.gallery-toggle{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .6875em 0;
    &._open{
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        background-image: url(../img/burger2.svg);
      }
    }
    &__inner{
      &:after{
        display: none;
      }
    }
    &__logo{
      position: relative;
      z-index: 15;
      width: 6.5625em;
    }
    &._fix{
      padding: .625em 0;
      .header{
        &__logo{
          width: 5.9375em;
        }
      }
    }
  }

  .nav{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: $gray;
    padding: 7.5em 1.25em 4.375em;
    display: flex;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 14;
    &__link{
      border: none;
      &:not(:last-child){
        margin: 0 0 1.75em;
      }
    }
  }

  .burger{
    position: relative;
    z-index: 15;
    display: flex;
    width: 1.375em;
    height: 1.375em;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
  }

  .gallery-toggle{
    position: relative;
    z-index: 15;
    display: flex;
    width: 1.125em;
    height: 1.125em;
    background-image: url(../img/gallery2.svg);
    @include bgImgSize(100%);
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .5em 0;
    &._fix{
      padding: .5625em 0;
      .header{
        &__logo{
          width: 4.1875em;
        }
      }
    }
  }
}
