@import "../mixins";
.gallery{
  height: 100vh;
  overflow: hidden;
  &__wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  &._disable{
    pointer-events: none;
  }
}

.gallery-slide{
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &:nth-child(odd){
    background-image: url(../img/gallery-bg.png);
  }
  .container{
    max-width: 1540px;
    padding-left: 16.25em;
    padding-right: 13.75em;
  }
  &__inner{
    height: 100vh;
    padding: 5.125em 0 3em;
    display: grid;
    grid-template-columns: 61% 33%;
    justify-content: space-between;
    align-items: center;
  }
}

.gallery{
  position: relative;
  &__outline{
    position: relative;
  }
  &__img{
    &-wrap{
      @include center;
    }
    img{
      max-height: calc(100vh - 8.125em);
      max-width: 100%;
      transform: translateY(1.5em);
    }
  }
  &__title{
    font-weight: 600;
    font-size: 2.375em;
    line-height: 161%;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__info{
    margin-top: 1.5em;
    display: flex;
    &-group{
      &:not(:last-child){
        margin-right: 3.75em;
      }
    }
    &-title{
      font-weight: 500;
      font-size: 1em;
      line-height: 125%;
      color: #858585;
    }
    &-desc{
      font-weight: 700;
      font-size: 1.375em;
      line-height: 123%;
    }
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
  &__btn{
    padding: 0 2.25em;
  }
  &__box{
    position: absolute;
    width: 21.625em;
    left: 100%;
    top: 100%;
    margin-top: -12.5em;
    margin-left: -15.3125em;
    z-index: 2;
    transition: all .3s;
    img{
      width: 100%;
    }
  }
}

.gallery-nav{
  position: absolute;
  left: 2.5em;
  top: 0;
  height: 100%;
  padding: 5.8125em 0;
  overflow: hidden;
  max-width: 12.5em;
  display: flex;
  align-items: center;
  &__links{
    max-height: 100%;
    overflow: hidden;
    @include grid100gap(1.125em);
    transition: all .3s;
    opacity: 1;
    &._disable{
      opacity: 0;
    }
  }
  &__link{
    color: #666666;
    span{
      font-size: .875em;
      line-height: 121%;
    }
    &:hover{
      cursor: pointer;
      color: $black;
    }
    &._active{
      grid-row: 8/9;
      font-weight: 700;
      padding-top: 1.125em;
      border-top: 1px solid #B6B6B6;
      span{
        font-size: 1.0625em;
      }
    }
  }
  &__buttons{
    grid-row: 9/10;
    display: flex;
    align-items: center;
    padding-bottom: 1.125em;
    border-bottom: 1px solid #B6B6B6;
  }
  &__btn{
    width: 1.5625em;
    height: 1.5625em;
    background: #F9F9F9;
    margin-right: .3125em;
    flex-shrink: 0;
  }
}

.thejloupe{
  border-radius: 50% !important;
  width: 15.625em !important;
  height: 15.625em !important;
  border: 4px solid #fff !important;
  z-index: 100;
}

.thejloupeview{
  border-radius: 50% !important;
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
}


@media screen and (max-width: 1400px){
  .gallery-slide{
    .container{
      padding-left: 13em;
      padding-right: 13em;
    }
    &__inner{
      grid-template-columns: 55% 42%;
    }
  }

  .gallery-nav{
    left: 1em;
  }
}


@media screen and (max-width: 992px){
  .gallery{
    &__box{
      display: none;
    }
    &__img{
      &-wrap{
        height: auto;
      }
      img{
        max-height: calc(100vh - 30.675em);
      }
    }
    &__content{
      flex-shrink: 0;
      height: 14.625em;
      margin-top: 2.25em;
    }
    &__title{
      text-align: center;
    }
    &__desc{
      @include truncate(4);
      margin-top: 1.125em;
      p{
        display: inline;
      }
    }
    &__bottom{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 1.125em;
    }
    &__info{
      margin: 0;
    }
    &__nav{
      margin: 0 0 0 1em;
    }
  }

  .gallery-nav{
    display: none;
  }

  .gallery-slide{
    .container{
      padding-left: 3.5em;
      padding-right: 3.5em;
    }
    &__inner{
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 8.125em;
      padding-bottom: 5.625em;
    }

  }

  .thejloupe{
    transform: translateX(-80%);
    display: none !important;
  }
}


@media screen and (max-width: 576px){
  .gallery{
    &__img{
      &-wrap{
        height: auto;
      }
      img{
        max-height: calc(100vh - 19.3125em);
        transform: translateX(2%);
      }
    }
    &__content{
      height: 13.1875em;
      margin-top: .875em;
    }
    &__title{
      font-size: 1.5em;
      line-height: 119%;
    }
    &__desc{
      @include truncate(3);
      margin-top: 1em;
      p{
        font-size: .75em;
      }
    }
    &__bottom{
      display: block;
      margin-top: 1em;
    }
    &__info{
      &-group{
        display: flex;
        &:not(:last-child){
          margin-right: 2.5em;
        }
      }
      &-title,
      &-desc{
        font-size: .875em;
        line-height: 121%;
      }
    }
    &__nav{
      margin: 1em 0 0 0 ;
    }
    &__btn{
      width: 100%;
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }

  .gallery-slide{
    .container{
      padding-left: 1.125em;
      padding-right: 1.125em;
    }
    &__inner{
      padding-top: 4.125em;
      padding-bottom: 1.125em;
    }

  }
}
